@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
body {
  margin: 0;
  font-family: "Caveat", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(150, 150, 150);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  text-align: center;
  box-sizing: border-box;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: purple;
}

@media all and (min-width: 700px) {
  .App-header {
    font-size: calc(10px + 3vmin);
  }
}

.counter {
  border: 1px solid purple;
  padding: 30px;
  width: 60%;
  margin: 40px auto;
  font-size: 22px;
}

.counter span {
  color: purple;
  margin-left: 10px;
}

.counter p {
  margin: 0;
}

.buttons-pannel {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  margin: 10px 0px;
}

button {
  box-sizing: border-box;
  width: 180px;
  margin: 5px 0px 0px 0px;
  padding: 10px 40px;
  border: 1px solid purple;
  color: purple;
  text-transform: uppercase;
  background-color: transparent;
  cursor: pointer;
  outline: 0px;
  font-family: "Caveat", cursive;
  font-size: 15px;
}

button:hover {
  transition: 0.2s;
  box-shadow: 0px 5px 15px rgba(145, 92, 182, 2);
}

.buttons-pannel input {
  margin-top: 10px;
  color: purple;
  outline: 0px;
  padding: 5px 5px;
  font-size: 15px;
  border: 1px solid purple;
  background-color: transparent;
  font-family: "Caveat", cursive;
}

@media all and (min-width: 700px) {
  .buttons-pannel {
    flex-direction: row;
  }
  button {
    flex-basis: 32%;
  }
}

input {
  display: block;
  margin: 0 auto;
  width: 30%;
}

@media all and (min-width: 700px) {
  input {
    width: 15%;
  }
}

